export default {
    mounted() {
        setTimeout(() => {
            this.services.forEach(service => {
                service.loading = false;
            });
        }, 1000);

        this.getGreeting();
        this.getRandomQuote();
    },
    data() {
        return {
            greeting: '',
            quotes: [
                "hier siehst du, was heute abgeht!",
                "entdecke, was heute los ist!",
                "sieh dir die neuesten Aktivitäten an!",
                "hier gibt’s alles, was du heute wissen musst!",
                "hier erfährst du, was heute bei uns auf dem Plan steht!",
            ],
            randomQuote: "",
            services: [
                { name: 'Small + Domain', server: 'gs-host-04', type: 0, cpu: 20, ram: 65, storage: 89, network: 12, status: 0, expired: '', loading: true },
                { name: 'MC-VPS', server: 'gs-host-23', type: 1, cpu: 54, ram: 32, storage: 21, network: 34, status: 1, expired: '', loading: true },
                { name: 'Gucci-Business', server: 'gs-host-07', type: 2, cpu: 76, ram: 76, storage: 34, network: 12, status: 2, expired: '', loading: true },
                { name: 'MeinDEDI!', server: 'gs-host-04', type: 3, cpu: 12, ram: 98, storage: 12, network: 22, status: 3, expired: '', loading: true },
                { name: 'Geheimer VPN', server: 'vpn-host-02', type: 4, cpu: 12, ram: 98, storage: 12, network: 22, status: 4, expired: '', loading: true },
                { name: 'Plesk License', server: 'licenses', type: 5, cpu: 12, ram: 98, storage: 12, network: 22, status: 5, expired: '', loading: true },
            ],
            serviceTypes: {
                0: 'Webhosting',
                1: 'VPS Server',
                2: 'KVM Machine',
                3: 'Dedicated Server',
                4: 'VPN Server',
                5: 'License'
            },
            serviceIcons: {
                0: 'fa-solid fa-globe',
                1: 'fa-solid fa-box',
                2: 'fa-solid fa-microchip',
                3: 'fa-solid fa-server',
                4: 'fa-solid fa-shield',
                5: 'fa-solid fa-key'
            }
        };
    },
    methods: {
        getGreeting() {
            const hour = new Date().getHours();
            if (hour >= 5 && hour < 12) {
                this.greeting = "Guten Morgen";
            } else if (hour >= 12 && hour < 18) {
                this.greeting = "Guten Tag";
            } else if (hour >= 18 && hour < 22) {
                this.greeting = "Guten Abend";
            } else {
                this.greeting = "Gute Nacht";
            }
        },
        getRandomQuote() {
            const randomIndex = Math.floor(Math.random() * this.quotes.length);
            this.randomQuote = this.quotes[randomIndex];
        },
    }
}
</script>
